/*
base on design of https://sports-fashion-theme.myshopify.com
*/
* {
    font-family: 'Source Sans Pro', sans-serif;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
$hover-color-red: rgb(211, 9, 9);
$hover-color-gray: rgb(245, 245, 245);
$white-color: rgb(223, 223, 223);
$gray-border: rgb(207, 205, 205);
/*----- top-bar (logo, date time, currency) -----*/
.top-bar {
    .container {
        .row {
            -webkit-box-align: center;
                -ms-flex-align: center;
                    align-items: center;
            padding-top: 10px;
            padding-bottom: 10px;
            /* logo */
            .col-2.logo-container {
                .logo {
                    img {
                        height: 35px;
                    }
                }
                
            }
            /* clock */
            .col-4.date-time-container{
                .date-time {
                    padding: 5px 0;
                    display: inline-block;
                }
            }
            /* latest collection*/
            .col-3.lastest {
                a {
                    display: -webkit-box;
                    display: -ms-flexbox;
                    display: flex;
                    -webkit-box-align: center;
                        -ms-flex-align: center;
                            align-items: center;
                    color: black;
                    text-decoration: none;
                    -webkit-transition: .3s;
                    -o-transition: .3s;
                    transition: .3s;
                    img {
                        width: 50px;
                        height: 50px;
                        margin-left: 15px
                    }
                    &:hover {
                        color: $hover-color-red;
                    }
                }
            }
            /* currency */
            .col-auto {
                -webkit-box-flex: 1;
                    -ms-flex: 1;
                        flex: 1;
                text-align: right;
                /* wrap all content */
                .account-currency {
                    float: right;
                    position: relative;
                    /* "Currency" and name of currency */
                    a, span {
                        display: inline-block;
                        padding: 0 10px;
                        color: black;
                        &:hover {
                            color: $hover-color-red;
                        }
                    }
                    .currency {
                        cursor: pointer;
                        display: -webkit-inline-box;
                        display: -ms-inline-flexbox;
                        display: inline-flex;
                        -webkit-box-align: center;
                            -ms-flex-align: center;
                                align-items: center;
                        -webkit-box-pack: end;
                            -ms-flex-pack: end;
                                justify-content: flex-end;
                        &:hover {
                            span, .icon {
                                color: $hover-color-red
                            }
                        }
                    }
                    /* drop down icon */
                    .icon {
                        font-size: 13px;
                        margin-right: 3px;
                        display: inline-block;
                        color: rgb(102, 101, 101);
                        &:hover {
                            color: $hover-color-red;
                        }
                    }
                    /* top up (show when click on name of currency or drop down icon */
                    .currency-list {
                        text-align: left;
                        position: absolute;
                        z-index: 1000;
                        width: 100%;
                        top: 100%;
                        max-height: 0;
                        background: white;
                        overflow: hidden;
                        -webkit-transition: .3s;
                        -o-transition: .3s;
                        transition: .3s;
                        ul {
                            margin: 0;
                            padding: 0;
                            list-style-type: none;
                            li {
                                padding: 10px;
                                cursor: pointer;
                                display: -webkit-box;
                                display: -ms-flexbox;
                                display: flex;
                                -webkit-box-align: center;
                                    -ms-flex-align: center;
                                        align-items: center;
                                &:hover {
                                    color: $hover-color-red;
                                    text-decoration: underline;
                                    background: $hover-color-gray;
                                    * {
                                        color: $hover-color-red;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
/*----- end top bar -----*/
/*----- button back to top -----*/
.back-top-btn {
    width: 40px;
    height: 40px;
    position: fixed;
    right: 10px;
    bottom: 30px;
    background: black;
    border-radius: 10px;
    border: 1px solid white;
    cursor: pointer;
    color: white;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
    .icon {
        margin: auto;
    }
}
/*----- end button back to top -----*/
/*----- footer -----*/
.footer {
    background: black;
    color: $white-color;
    padding: 30px 0;
    .row {
        .title {
            font-size: 25px;
            margin-bottom: 15px;
        }
        .title.social {
            margin-top: 30px;
        }
        /* about and newsletter */
        .col-3 {
            .payment {
                img {
                    width: 100%;
                    margin: 15px 0 20px 0;
                }
            }
            /* subscribe-form at newsletter */
            .subscribe-form {
                border: 1px solid white;
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                padding: 5px;
                input, button {
                    border: none;
                    outline: none;
                    background: transparent;
                    color: $white-color;
                }
                input {
                    -webkit-box-flex: 1;
                        -ms-flex: 1;
                            flex: 1;
                }
                ::-webkit-input-placeholder {
                    color: $white-color;
                }
                ::-moz-placeholder {
                    color: $white-color;
                }
                :-ms-input-placeholder {
                    color: $white-color;
                }
                ::-ms-input-placeholder {
                    color: $white-color;
                }
                ::placeholder {
                    color: $white-color;
                }
            }
            /* social-links icons: face, twitter,... */
            .social-links {
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -webkit-box-pack: justify;
                    -ms-flex-pack: justify;
                        justify-content: space-between;
                a {
                    display: block;
                    width: 40px;
                    height: 40px;
                    border: 1px solid $white-color;
                    line-height: 40px;
                    border-radius: 50%;
                    text-align: center;
                    .icon {
                        color: $white-color
                    }
                    &:hover {
                        border: 1px solid $hover-color-red;
                        .icon {
                            color: $hover-color-red
                        }
                    }
                }
            }
        }
        /* The Service, Information, Gallery */
        .col-2 {
            a {
                color: $white-color;
                display: block;
                -webkit-transition: .3s;
                -o-transition: .3s;
                transition: .3s;
                margin-bottom: 5px;
                &:hover {
                    color: $hover-color-red;
                }
            }
        }
    }
}
/*----- end footer -----*/
/*----- top nav -----*/
.top-nav {
    background: black;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    .container-fluid {
        .row {
            -webkit-box-align: center;
                -ms-flex-align: center;
                    align-items: center;
            .col {
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                /* nav: home icon, links */
                .nav {
                    width: 95%; /* width of nav */
                    > ul {
                        padding: 0;
                        margin: 0;
                        list-style-type: none;
                        display: -webkit-box;
                        display: -ms-flexbox;
                        display: flex;
                        -webkit-box-align: center;
                            -ms-flex-align: center;
                                align-items: center;
                        -ms-flex-wrap: wrap;
                            flex-wrap: wrap;
                        .active {
                            color: $hover-color-red;
                            text-decoration: underline;
                        }
                        > li {
                            margin-right: 20px; /* space between li tag */
                            position: relative;
                            > a {
                                color: $white-color;
                                display: block;
                                -webkit-transition: .3s;
                                -o-transition: .3s;
                                transition: .3s;
                                font-weight: bold;
                                font-size: 17px; /* font size of links */
                                &:hover {
                                    color: $hover-color-red;
                                }
                                .icon {
                                    margin-left: 5px;
                                    font-size: 10px;
                                }
                            }
                            /* sub menu */
                            .sub-menu {
                                position: absolute;
                                z-index: 99;
                                background: white;
                                display: -webkit-box;
                                display: -ms-flexbox;
                                display: flex;
                                -webkit-box-orient: vertical;
                                -webkit-box-direction: normal;
                                    -ms-flex-direction: column;
                                        flex-direction: column;
                                list-style-type: none;
                                padding: 0;
                                margin: 0;
                                display: none;
                                li {
                                    a {
                                        color: black;
                                        -webkit-transition: .3s;
                                        -o-transition: .3s;
                                        transition: .3s;
                                        font-weight: bold;
                                        display: block;
                                        padding: 10px;
                                        white-space: nowrap;
                                        &:hover {
                                            color: $hover-color-red;
                                            background: $hover-color-gray;
                                        }
                                    }
                                }
                            }
                            &:hover {
                                .sub-menu {
                                    display: -webkit-box;
                                    display: -ms-flexbox;
                                    display: flex;
                                }
                            }
                            &:last-child {
                                margin-right: 0;
                            }
                        }
                        /* home icon */
                        .home {
                            padding: 8px 0; /* this controll height of top - nav */
                            a {
                                .icon {
                                    font-size: 18px !important;
                                    margin-left: 0;
                                }
                            }
                        }
                    }
                    
                }
                /* end nav: home icon, links */
                /* shopping cart */
                .cart {
                    width: 5%;
                    display: -webkit-box;
                    display: -ms-flexbox;
                    display: flex;
                    -webkit-box-orient: vertical;
                    -webkit-box-direction: normal;
                        -ms-flex-direction: column;
                            flex-direction: column;
                    -webkit-box-pack: center;
                        -ms-flex-pack: center;
                            justify-content: center;
                    .cart-amount {
                        text-align: right;
                        position: relative;
                        display: -webkit-box;
                        display: -ms-flexbox;
                        display: flex;
                        -webkit-box-pack: end;
                            -ms-flex-pack: end;
                                justify-content: flex-end;
                        -webkit-box-align: center;
                            -ms-flex-align: center;
                                align-items: center;
                        cursor: pointer;
                        /* cart icon */
                        .icon {
                            fill: $white-color; /* use flat icon */
                            width: 20px;
                            height: 20px;
                        }
                        /* cart quantity (the red box) */
                        .current-amount{
                            color: $white-color;
                            font-size: 20px;
                            font-weight: bold;
                            display: inline-block;
                            background: red;
                            padding: 0 10px;
                            margin-left: 10px;
                        }
                        /* cart top up when hove on icon cart or the red box*/
                        .cart-top-up {
                            -webkit-box-shadow: 1px 3px 4px rgb(197, 197, 197);
                                    box-shadow: 1px 3px 4px rgb(197, 197, 197);
                            position: absolute;
                            top: 100%;
                            width: 650%; /* width of top up */
                            right: 0;
                            z-index: 100;
                            background: white;
                            text-align: left;
                            font-size: 13px;
                            display: none;
                            /* delete icon */
                            .icon {
                                width: 15px;
                                height: 15px;
                                &:hover {
                                    color: $hover-color-red !important;
                                }
                            }
                            /* Recently added 3 items(s) */
                            .title {
                                padding: 5px 15px 10px 15px;
                                font-weight: bold;
                            }
                            /* empty top up if no product added to cart */
                            .empty-cart {
                                display: -webkit-box;
                                display: -ms-flexbox;
                                display: flex;
                                .content {
                                    margin: auto;
                                    text-align: center;
                                    padding: 20px;
                                    p {
                                        margin: 0;
                                    }
                                    .announce {
                                        font-weight: bold;
                                    }
                                }
                            }
                            /* detail top up if product(s) added to cart */
                            .added-cart {
                                /* display each product in shopping cart top up */
                                .added-product {
                                    margin-top: 10px;
                                    padding-bottom: 2px;
                                    /* each product is contained in a container-fluid */
                                    .container-fluid {
                                        .row {
                                            -webkit-box-align: start;
                                                -ms-flex-align: start;
                                                    align-items: flex-start;
                                            /* col image */
                                            .img {
                                                padding-right: 0;
                                                img {
                                                    width: 100%;
                                                    height: 90px;
                                                }
                                            }
                                            /* col name */
                                            .title {
                                                text-align: left;
                                                padding: 0 10px;
                                                font-size: 16px;
                                                margin-top: -3px;
                                                a {
                                                    color: black;
                                                }
                                            }
                                            /* col containing delete icon and quantity and price */
                                            .amount {
                                                height: 90px;
                                                .cal {
                                                    height: 100%;
                                                    display: -webkit-box;
                                                    display: -ms-flexbox;
                                                    display: flex;
                                                    -webkit-box-orient: vertical;
                                                    -webkit-box-direction: normal;
                                                        -ms-flex-direction: column;
                                                            flex-direction: column;
                                                    -webkit-box-align: center;
                                                        -ms-flex-align: center;
                                                            align-items: center;
                                                    -webkit-box-pack: justify;
                                                        -ms-flex-pack: justify;
                                                            justify-content: space-between;
                                                    /* delete icon */
                                                    .icon {
                                                        color: black;
                                                    }
                                                    /* quantity and price */
                                                    .money {
                                                        display: -webkit-box;
                                                        display: -ms-flexbox;
                                                        display: flex;
                                                        -webkit-box-orient: vertical;
                                                        -webkit-box-direction: normal;
                                                            -ms-flex-direction: column;
                                                                flex-direction: column;
                                                        font-weight: bold;
                                                        text-align: center;
                                                        font-size: 16px;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                                /* total amount of shopping cart */
                                .total {
                                    text-align: right;
                                    padding: 15px;
                                    font-weight: bold;
                                    font-size: 19px;
                                }
                                /* button view cart and check out */
                                .navigate {
                                    padding: 0;
                                    margin: 0;
                                    .col {
                                        padding: 0 15px;
                                        .content {
                                            text-align: center;
                                            padding: 5px;
                                            font-size: 15px;
                                            a {
                                                color: $white-color;
                                                display: block;
                                            }
                                        }
                                        /* button view cart */
                                        .view {
                                            background: black;
                                            width: 100%;
                                            font-weight: bold;
                                        }
                                        /* button vcheck out */
                                        .check-out {
                                            background: red;
                                            width: 100%;
                                            font-weight: bold;
                                        }
                                    }
                                }
                            }
                        }
                        /* show cart top up when hover on cart icon or cart amount (red box) */
                        &:hover {
                            .cart-top-up {
                                display: block;
                            }
                        }
                    }
                }
            }
        }
    }
}
/*----- end top nav -----*/
/*----- top slide -----*/
.home {
    padding: 20px 0;
    /*----- use Boostrap Carousel combined library https://animate.style -----*/
    .top-slide {
        height: 400px; /* height of visible content */
        overflow: hidden;
        .container-fluid {
            .row {
                .col {
                    padding: 0;
                    #top-slide {
                        .carousel-inner {
                            .carousel-item {
                                /*----- carousel-caption -----*/
                                .carousel-caption {
                                    padding: 160px 0 0 0;
                                    top: 0;
                                    z-index: 0.5;
                                    left: 12%;
                                    right: 12%;
                                    .animate__animated {
                                        -webkit-animation-duration: .7s;
                                                animation-duration: .7s;
                                    }
                                    .title {
                                        -webkit-animation-delay: .3s;
                                                animation-delay: .3s;
                                    }
                                    .des {
                                        -webkit-animation-delay: .7s;
                                                animation-delay: .7s;
                                    }
                                    .link {
                                        -webkit-animation-delay: 1s;
                                                animation-delay: 1s;
                                    }
                                }
                                .carousel-caption.slide-1, .carousel-caption.slide-3 {
                                    text-align: left;
                                    .title {
                                        margin-bottom: 40px;
                                        span {
                                            background: white;
                                            color: black;
                                            padding: 10px;
                                            border-radius: 10px;
                                            font-size: 40px;
                                        }
                                    }
                                    .des {
                                        margin-bottom: 40px;
                                        font-size: 20px;
                                        background: white;
                                        color: black;
                                        font-style: italic;
                                        padding: 0 10px;
                                        border-radius: 10px;
                                    }
                                    .link {
                                        a {
                                            font-size: 20px;
                                            background: white;
                                            color: black;
                                            font-weight: bold;
                                            padding: 10px;
                                            border-radius: 10px;
                                            -webkit-transition: .3s;
                                            -o-transition: .3s;
                                            transition: .3s;
                                            &:hover {
                                                color: $hover-color-red
                                            }
                                        }
                                    }
                                }
                                .carousel-caption.slide-2 {
                                    text-align: center;
                                    .title {
                                        margin: 30px;
                                        span {
                                            color: white;
                                            font-size: 40px;
                                            font-weight: bold;
                                        }
                                    }
                                    .des {
                                        margin-bottom: 20px;
                                        font-size: 20px;
                                        color: white;
                                        font-style: italic;
                                        font-weight: bold;
                                    }
                                    .link {
                                        a {
                                            font-size: 20px;
                                            background: white;
                                            color: black;
                                            font-weight: bold;
                                            padding: 10px;
                                            border-radius: 10px;
                                            -webkit-transition: .3s;
                                            -o-transition: .3s;
                                            transition: .3s;
                                            &:hover {
                                                color: $hover-color-red
                                            }
                                        }
                                    }
                                }
                                .carousel-caption.slide-3 {
                                    text-align: right;
                                    .title {
                                        float:  left !important;
                                    }
                                    .des {
                                        clear: left;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
/*----- end top slide -----*/
/*----- brand slide (Rolex, Livie's,...), use Tiny Slider React -----*/
.brand-slide {
    .container {
        .row {
            .col {
                .tns-outer {
                    position: relative;
                    height: 150px; /* height of the whole slide */
                    display: -webkit-box;
                    display: -ms-flexbox;
                    display: flex;
                    -webkit-box-align: center;
                        -ms-flex-align: center;
                            align-items: center;
                    cursor: pointer;
                    .brand-item {
                        text-align: center;
                    }
                    /* container of arrows previous and next */
                    .tns-controls {
                        position: absolute;
                        width: 100%;
                        display: -webkit-box;
                        display: -ms-flexbox;
                        display: flex;
                        -webkit-box-pack: justify;
                            -ms-flex-pack: justify;
                                justify-content: space-between;
                        top: 35%;
                        z-index: 1;
                        button {
                            background-color: transparent !important;
                            border: none;
                            outline: none;
                            color: transparent !important;
                            background : {
                                size: 70%;
                                repeat: no-repeat;
                            }
                            width: 50px; /* width and height of each arrow */
                            height: 50px;
                            &:first-child {
                                background : {
                                    image: url("../images/section-brand-slide/pre.svg");
                                    position: center left;
                                }
                            }
                            &:last-child {
                                background : {
                                    image: url("../images/section-brand-slide/next.svg");
                                    position: center right;
                                }
                            }
                        }
                    }
                    /*----- hide the default stop/start button of Tiny Slider React -----*/
                    button[data-action=stop], button[data-action=start] {
                        display: none;
                    }
                }
            }
        }
    }
}
/*----- end brand slide -----*/
/*----- collection sum (list all collections) -----*/
.collection-sum {
    .container {
        .row {
            height: 510px;
            margin: 0;
            padding: 0;
            overflow: hidden;
            /* all collections */
            .col-4.all {
                margin: 0;
                padding: 0;
                overflow: hidden;
                a {
                    text-decoration: none;
                    /* wrap all contents inside, will be zoomed out when hover */
                    .wrapper {
                        height: 100%;
                        -webkit-transition: .3s;
                        -o-transition: .3s;
                        transition: .3s;
                        background : {
                            image: url("../images/section-collection-sum/all.jpg");
                            size: cover;
                            position: center;
                            repeat: no-repeat;
                        }
                        .content {
                            text-align: right;
                            color: white;
                            font-weight: bold;
                            padding-top: 30%;
                            padding-right: 20px;
                            /* OFF 50% */
                            .off {
                                background: rgba($color: #000000, $alpha: 0.7);
                                display: -webkit-inline-box;
                                display: -ms-inline-flexbox;
                                display: inline-flex;
                                -webkit-box-orient: vertical;
                                -webkit-box-direction: normal;
                                    -ms-flex-direction: column;
                                        flex-direction: column;
                                height: 85px;
                                width: 85px;
                                border-radius: 50%;
                                -webkit-box-pack: center;
                                    -ms-flex-pack: center;
                                        justify-content: center;
                                -webkit-box-align: center;
                                    -ms-flex-align: center;
                                        align-items: center;
                                span {
                                    &:first-child {
                                        font-size: 20px;
                                    }
                                }
                            }
                            /* title ALL COLLECTIONS */
                            .title {
                                background: rgba($color: #000000, $alpha: 0.8);
                                margin: 20px 0;
                                display: inline-block;
                                padding: 10px;
                            }
                            /* FOR WOMEN */
                            .des {
                                background: rgba($color: red, $alpha: 0.8);
                                display: inline-block;
                                padding: 10px;
                            }
                        }
                    }
                    
                }
                /* zoom out wrapper when hover */
                &:hover {
                    .wrapper {
                        -webkit-transform: scale(1.1);
                            -ms-transform: scale(1.1);
                                transform: scale(1.1)
                    }
                }
            }
            /* each collections */
            .col-8 {
                margin: 0;
                padding: 0;
                .row {
                    height: 50%; /* height of each collection */
                    .col-4 {
                        padding: 0; 
                        position: relative;
                        overflow: hidden;
                        a {
                            color: white;
                            /* background image, will be zoomed out when hover */
                            .background {
                                position: absolute;
                                top: 0;
                                right: 0;
                                bottom: 0;
                                left: 0;
                                z-index: -1;
                                -webkit-transition: .3s;
                                -o-transition: .3s;
                                transition: .3s;
                                background : {
                                    size: cover;
                                    position: center;
                                    repeat: no-repeat;
                                }
                            }
                            .featured {
                                background : {
                                    image: url("../images/section-collection-sum/featured.jpg");
                                }
                            }
                            .hot {
                                background : {
                                    image: url("../images/section-collection-sum/hot.jpg");
                                }
                            }
                            .latest {
                                background : {
                                    image: url("../images/section-collection-sum/latest.jpg");
                                }
                            }
                            .minimal {
                                background : {
                                    image: url("../images/section-collection-sum/minimal.jpg");
                                }
                            }
                            .modern {
                                background : {
                                    image: url("../images/section-collection-sum/modern.jpg");
                                }
                            }
                            .summer {
                                background : {
                                    image: url("../images/section-collection-sum/summer.jpg");
                                }
