                                    }
                                    .price {
                                        font-weight: bold;
                                        font-size: 13px;
                                    }
                                }
                            }
                            /* overlay appear when hover on product */
                            .overlay {
                                position: absolute;
                                top: 0;
                                left: -100%;
                                width: 100%;
                                height: 241px; /* height of image of overlay */
                                display: -webkit-box;
                                display: -ms-flexbox;
                                display: flex;
                                -webkit-box-orient: vertical;
                                -webkit-box-direction: normal;
                                    -ms-flex-direction: column;
                                        flex-direction: column;
                                -webkit-transform: scale(0);
                                    -ms-transform: scale(0);
                                        transform: scale(0);
                                -webkit-transition: .5s;
                                -o-transition: .5s;
                                transition: .5s;
                                /* background image */
                                .blur {
                                    -webkit-box-flex: 1;
                                        -ms-flex: 1;
                                            flex: 1;
                                    background : {
                                        position: center;
                                        size: cover;
                                        repeat: no-repeat;
                                    }
                                }
                                /* container button add to cart and zoom out */
                                .cart-zoom {
                                    background: black;
                                    -webkit-transition: .3s;
                                    -o-transition: .3s;
                                    transition: .3s;
                                    .row {
                                        margin: 0;
                                        .col-6 {
                                            padding: 0;
                                            .icon-container {
                                                height: 25px; /* height of icon bar */
                                                display: -webkit-box;
                                                display: -ms-flexbox;
                                                display: flex;
                                                -webkit-box-align: center;
                                                    -ms-flex-align: center;
                                                        align-items: center;
                                                -webkit-transition: .3s;
                                                -o-transition: .3s;
                                                transition: .3s;
                                                /* button add to cart */
                                                .icon {
                                                    color: white;
                                                    margin: auto;
                                                    display: block;
                                                    width: 15px;
                                                    height: 15px;
                                                }
                                                &:hover {
                                                    background: red;
                                                }
                                                /* button zoom out, will top op the modal detail */
                                                button {
                                                    background: green;
                                                    border: none !important;
                                                    outline: none !important;
                                                    width: 100%;
                                                    height: 100%;
                                                    border-radius: 0;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                            /* show overlay when hover product */
                            &:hover {
                                .overlay {
                                    left: 0;
                                    -webkit-transform: scale(1);
                                        -ms-transform: scale(1);
                                            transform: scale(1);
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
