/*----- shopping cart detail, appear when click "View Cart" while cart is not empty -----*/
.shopping-cart-detail {
    padding-top: 20px;
    .container {
        .row.title {
            /* big title at top (Your Shopping Cart) */
            .col {
                h4 {
                    position: relative;
                    font-weight: bold;
                    font-size: 20px;
                    margin-bottom: 20px;
                    display: inline-block;
                    &::after {
                        content: "";
                        position: absolute;
                        top: 100%;
                        left: 0;
                        width: 100%;
                        border-top: 2px solid red;
                    }
                }
            }
            /* button continue shopping on the right side */
            .col-auto {
                a {
                    color: black;
                    text-decoration: underline;
                    .icon {
                        margin-right: 3px;
                    }
                    -webkit-transition: .3s;
                    -o-transition: .3s;
                    transition: .3s;
                    &:hover {
                        color: $hover-color-red;
                    }
                }
            }
        }
        /* container of order summary on the left and total amount on the right */
        .row.shopping-cart-content {
            padding: unset;
            .content {
                border: 1px solid $hover-color-gray;
                    .title {
                        background: $hover-color-gray;
                        font-weight: bold;
                        padding: 10px;
                    }
            }
            /* order summary on the left */
            .col-6.order {
                padding-top: 0;
                .content {
                    .info {
                        .container {
                            padding-top: 10px;
                            /* each product is a row */
                            .product-row {
                                margin-bottom: 15px;
                                /* main image */
                                .col-2.img {
                                    padding-left: 10px;
                                    img {
                                        width: 55px;
                                        height: 80px
                                    }
                                }
                                /* container of name, price, quantity, button + -, update, remove */
                                .col-10.product-detail {
                                    .row {
                                        margin-bottom: 5px;
                                    }
                                    padding: 0;
                                    /* name */
                                    .row.name {
                                        font-weight: bold;
                                        a {
                                            color: black;
                                            -webkit-transition: .3s;
                                            -o-transition: .3s;
                                            transition: .3s;
                                            &:hover {
                                                color: $hover-color-red;
                                            }
                                        }
                                    }
                                    /* price */
                                    .row.price {
                                        font-weight: bold;
                                        font-size: 18px;
                                    }
