                                                margin-right: 3px;
                                            }
                                            &:hover {
                                                background: $hover-color-red;
                                                color: white;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            /* total amount on the right */
            .col-3.total-amount {
                .content {
                    .info {
                        /* total amount */
                        .money {
                            padding: 10px;
                            font-size: 30px;
                            font-weight: bold;
                        }
                        /* button check out */
                        .check-out {
                            padding: 10px;
                            .wrap {
                                padding: 5px;
                                background: $hover-color-red;
                                color: white;
                                font-weight: bold;
                                font-size: 20px;
                                .icon {
                                    margin-right: 5px;
                                }
                                a {
                                    color: white;
                                }
                            }
