/*----- check out, use for both whole shopping cart and separate product -----*/
.check-out {
    /* show errors if form is not filled correctly */
    .errors {
        color: blue;
        font-weight: bold;
        padding-left: 10px;
    }
    padding: 20px 0;
    .container {
        /* big title at top Welcome to Payment Portal */
        .row.title {
            .col {
                h4 {
                    position: relative;
                    font-weight: bold;
                    font-size: 25px;
                    margin-bottom: 20px;
                    display: inline-block;
                    &::after {
                        content: "";
                        position: absolute;
                        top: 100%;
                        left: 0;
                        width: 50%;
                        border-top: 2px solid red;
                    }
                }
            }
        }
        /* container of forms in the left and order summary on the right */
        .row.payment {
            /* forms in the left */
            .col-7.payment-process {
                .content {
                    /* name of form: Shipping Address, Billing Address, Payment */
                    .form-field {
                        font-weight: bold;
                        font-size: 20px;
                    }
                    /* general style of all forms */
                    form {
                        display: -webkit-box;
                        display: -ms-flexbox;
                        display: flex;
                        -webkit-box-orient: vertical;
                        -webkit-box-direction: normal;
                            -ms-flex-direction: column;
                                flex-direction: column;
                        /* input tag */
                        .field {
                            margin: 5px 0;
                            padding: 10px;
                            border-radius: 10px;
                            border: 1px solid $gray-border;
                            color: rgb(51, 51, 51);
                            outline: none;
                        }
                    }
                    /* payment form */
                    .check-out-form.payment {
                        form {
                            .field {
                                margin-top: 2px;
                                margin-bottom: 2px;
                                padding: 10px;
                            }
                            .label{
                                font-weight: bold;
                                margin: 0;
                                margin-top: 10px;
                            }
                            /* remove default scroll of number input tag */
                            input::-webkit-outer-spin-button,
                            input::-webkit-inner-spin-button {
                            -webkit-appearance: none;
                            margin: 0;
                            }
                            /* Firefox */
                            input[type=number] {
                                -moz-appearance: textfield;
                            }
                        }
                    }
                    /* submit completed */
                    .check-out-form.completed {
                        /* title Congratulation, you had successful purchase */
                        .congrat {
                            h3 {
                                font-weight: bold;
                            }
                        }
                        /* container of order number, "You order number is:" & "You will need this number to review your order detail" */
                        .order-number {
                            .number {
                                font-weight: bold;
                            }
                            h5 {
                                &:last-child{
                                    text-decoration: underline;
                                }
                            }
                        }
                        /* container of button Review Order or Continue Shopping */
                        .navigate {
                            .col-6 {
                                padding: 0;
                                a {
                                    color: rgb(45, 45, 253);
                                    font-weight: bold;
                                    font-size: 20px;
                                    .icon {
                                        margin-right: 5px;
                                    }
                                }
                            }
                        }
                    }
                    /* nav button (back or next) for shipping, billing, payment */
                    .navigate { 
                        margin-top: 20px;
                        -webkit-box-align: center;
                            -ms-flex-align: center;
                                align-items: center;
                        display: -webkit-box;
                        display: -ms-flexbox;
                        display: flex;
                        .container {
                            .row {
                                -webkit-box-align: center;
                                    -ms-flex-align: center;
                                        align-items: center;
                                .col-6 {
                                    .wrap {
                                        text-align: center;
                                        border-radius: 10px;
                                        font-size: 18px;
                                        margin: auto;
                                        cursor: pointer;
                                        height: 100%;
                                        -webkit-transition: .3s;
                                        -o-transition: .3s;
                                        transition: .3s;
                                    }
                                    /* return button */
                                    .wrap.pre {
                                        background: black;
                                        color: white;
                                        a {
                                            color: white;
                                            display: block;
                                            text-decoration: none;
                                        }
                                        .icon {
                                            margin-right: 5px;
                                            display: inline-block;
                                        }
                                        &:hover {
                                            background: $hover-color-red;
                                        }
                                    }
                                    /* continue button */
                                    .wrap.next {
                                        input {
                                            background: $hover-color-red;
                                            color: white;
                                            padding: 0 10px;
                                            margin: 0;
                                            width: 100%;
                                            border: none;
                                            outline: none;
                                            border-radius: 10px;
                                            &:hover {
                                                background: black;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                    
                }
            }
            /* order summary on the right */
            .col-5.summary {
                /* title Your Order Summary */
                .title {
                    h5 {
                        font-weight: bold;
                        margin-bottom: 20px;
                    }
                }
                .content {
                    -webkit-box-shadow: 1px 1px 5px 5px $gray-border;
                            box-shadow: 1px 1px 5px 5px $gray-border;
                    padding: 0 5px;
                    table {
                        width: 100%;
                        font-size: 14px;
                        td {
                            padding: 10px 0;
                        }
                        /* render each product in a row, each product appear once only even when quantity is more than 1 */
                        tr.product {
                            /* name of product */
                            .name {
                                font-weight: bold;
                            }
                            /* quanity */
                            .quantity {
                                text-align: center;
                                color: $hover-color-red;
                                font-weight: bold;
                            }
                            .price {
                                text-align: right;
                            }
                            .amount {
                                font-weight: bold;
                                text-align: right;
                            }
                        }
                        /* total amount of order including product and shipping fee */
                        tr.total-amount {
                            font-size: 20px;
                            font-weight: bold;
                            td[colspan="3"]{
                                text-align: center;
                            }
                            .money {
                                text-align: right;
                            }
                        }
                    } 
                }
            }
        }
    }
}
/*----- end check out -----*/
